<template>
    <div class="publish">
        <el-dialog
            :title="parameter.title"
            :visible.sync="examineMessageDialogVisible"
            :close-on-click-modal="false"
            center 
            @open="handleOpen"
            width="80%">
            <el-form label-width="100px" :model="publishForm" class="publish_warp" ref="publishForm">
                <el-form-item label=":">
                    <span v-text="publishForm.title"></span>
                </el-form-item>
                <el-form-item label=":">
                        <img style="width:80px;height:80px" v-if="publishForm.photo" :src="publishForm.photo" class="avatar">
                </el-form-item>
                <!-- <el-form-item label="/:">
                    <span v-text="publishForm.releasename"></span>
                </el-form-item> -->
                <el-form-item label=":">
                    <span v-text="publishForm.sort"></span>
                </el-form-item>
                <el-form-item label=":" class="rich_text">
                    <!-- vue-ueditor-wrap -->
                    <vue-ueditor-wrap v-model="publishForm.content" :config="myConfig"></vue-ueditor-wrap>
                </el-form-item>
                 <el-form-item label=":" class="rich_text">
                    <el-input v-model="oppose" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="rejectBtn" class="rejectMessage"></el-button>
                <el-button type="primary" @click="adoptBtn" class="DetermineMessage"></el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name:'publish',
    props:['parameter'],
    data() {
        return {
            oppose:'',   //
            examineMessageDialogVisible: false,
            reject:{
                id: "",
                staus: "2"
            }, 
            publishForm: {
                title: '',
                // releasename: '',
                sort: '',
                photo: '',                                                                         
                content: ''
            },
            myConfig: {
                // 
                autoHeightEnabled: false,
                // 
                initialFrameHeight: 450,
                // 
                initialFrameWidth: '100%',
                // 
                serverUrl: 'http://35.201.165.105:8000/controller.php',
                // UEditor  vue-cli vue-ueditor-wrap 2
                UEDITOR_HOME_URL: process.env.BASE_URL + 'UEditor/'
            },
        }
    },
    methods:{
        handleChange (file, fileList) {
            this.publishForm.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeUpload(file) {
            return true;
        },
        rejectBtn(){
            this.reject.oppose = this.oppose;
            this.reject.staus = "3"
            this.parameter.fun(this.reject).then(res => {
                if(res.status == 200){
                    this.$message.success('')
                    this.examineMessageDialogVisible = false;
                    this.$parent.FindAll();
                }else{
                    this.$message.error('');
                }
            }).catch( err => {
                this.$message.error('');
            })
            
        },
        adoptBtn(){
            console.log(this.reject)
            this.parameter.fun(this.reject).then(res => {
                if(res.status == 200){
                    this.$message.success('')
                    this.examineMessageDialogVisible = false;
                    this.$parent.FindAll();
                }else{
                    this.$message.error('');
                }
            }).catch( err => {
                this.$message.error('');
            })
            
        },
        handleOpen(){
            if(this.parameter.data){
                this.publishForm = this.parameter.data;
                this.publishForm.photo = "http://" + this.parameter.data.tphoto
                this.reject.id = this.parameter.data.id;
            }else{
                this.publishForm = {
                    title: '',
                    // releasename: '',
                    sort: '',
                    photo: '',                                                                         
                    content: ''
                }
            }
            console.log(this.publishForm)
        }

    }
}
</script>

<style lang="scss">
    .rejectMessage{
        width: px2rem(120);
        border-radius: px2rem(18) !important;
        background: #F52653 !important;
        color: #ffffff !important;
        border: none !important;
        position: relative;
        right: -130px;
    }
</style>
