<template>
    <div class="publish">
        <el-dialog
            :title="parameter.title"
            :visible.sync="publishDialogVisible"
            :close-on-click-modal="false"
            center 
            @open="handleOpen"
            width="80%">
                <el-form label-width="100px" :model="publishForm" class="publish_warp" ref="publishForm" :rules="publishRule">
                    <el-form-item label=":" prop="title">
                        <el-input v-model="publishForm.title" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label=":" prop="photo">
                        <el-input v-model="publishForm.photo" v-if="false"></el-input>
                        <el-upload
                                id="avatar-uploader"
                                ref="upload"
                                :show-file-list="false"
                                :action="action" 
                                :before-upload="beforeUpload"
                                :on-change="handleChange"
                                :auto-upload="false"
                                :data="publishForm"
                                :on-success="publishSuc"
                                name="photo">  <!-- :data  action -->
                            <img v-if="publishForm.photo" :src="publishForm.photo" class="avatar">
                            
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    <!-- <el-form-item label="/:" prop="releasename">
                        <el-input v-model="publishForm.releasename" placeholder=""></el-input>
                    </el-form-item> -->
                    <el-form-item label=":" prop="sort">
                        <el-select v-model="publishForm.sort" placeholder="">
                        <el-option v-for="(list,index) in sort" :key="index" :label="list.sortname" :value="list.sortname"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label=":" class="rich_text" prop="content">
                        <!-- vue-ueditor-wrap -->
                        <vue-ueditor-wrap v-model="publishForm.content" :config="myConfig"></vue-ueditor-wrap>
                    </el-form-item>
                    <el-form-item>
                        <el-button class="previewBtn" @click="preview"></el-button>
                        <el-button class="saveDraftBtn" @click="saveDraft"></el-button>
                    </el-form-item>
                </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="publishDialogVisible = false" class="closeMessage"> </el-button>
                <el-button type="primary" @click="DetermineBtn" class="DetermineMessage"> </el-button>
            </span>
        </el-dialog>
        <preview ref="previewComponent" :publishForm="publishForm"></preview>
    </div>
</template>

<script>
import { addArticleUrl } from '../../service/index'
export default {
    name:'publish',
    props: ['parameter'],
    data() {
        return {
            publishDialogVisible:false,
            isTrue: false,  //messagBox
            action: addArticleUrl(), //
            sort:[],
            publishForm: {
                title: '',
                // releasename: '',
                sort: '',                                                               
                content: '',
                photo: ''
            },
            myConfig: {
                // 
                autoHeightEnabled: false,
                // 
                initialFrameHeight: 300,
                // 
                initialFrameWidth: '100%',
                // 
                serverUrl: 'http://35.201.165.105:8000/controller.php',
                // UEditor  vue-cli vue-ueditor-wrap 2
                UEDITOR_HOME_URL: process.env.BASE_URL + 'UEditor/'
            },
            publishRule:{
                title:[
                    { required: true, message: '', trigger: 'blur' }
                ],
                photo:[
                    { required: true, message: '', trigger: 'blur' }
                ],
                // releasename:[
                //     { required: true, message: '', trigger: 'blur' }
                // ],
                sort:[
                    { required: true, message: '', trigger: 'blur' }
                ],
                content:[
                    { required: true, message: '', trigger: 'blur' }
                ]

            }
        }
    },
    methods:{
        DetermineBtn() {
            let vm = this;
            this.$refs['publishForm'].validate((valid) => {
                if (valid) {
                    this.$refs.upload.submit();
                    this.$parent.FindAll();
                    this.publishDialogVisible = false //
                } else {
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                    return false;
                }
            });
        },
        handleChange (file) {
            this.publishForm.photo = URL.createObjectURL(file.raw);
        },
        beforeUpload(file) {
            return true;
        },
        preview(){
            // 
            this.$refs['publishForm'].validate((valid) => {
                if (valid) {
                    this.$refs.previewComponent.centerDialogVisible = true;   // 
                } else {
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                    return false;
                }
            })
        },
        saveDraft(){
            // 

        },
        handleOpen(){
            if(this.parameter.data){
                this.publishForm.title = this.parameter.data.title;
                // this.publishForm.releasename = this.parameter.data.releasename;
                this.publishForm.sort = this.parameter.data.sort;
                this.publishForm.photo = this.parameter.data.tphoto;
                this.publishForm.content = this.parameter.data.content;
                this.publishForm.id = this.parameter.data.id;
                this.sort = this.parameter.sort
            }else{
                this.publishForm = {
                    title: '',
                    // releasename: '',
                    sort: '',
                    photo: '',                                                                         
                    content: ''
                }
                this.sort = this.parameter.sort
            }
            
        },
        publishSuc(res){
            if(res.data.code == 200){
                this.$message.success(res.data.message);
            }else{
                this.$message.error('!');
            }
        }
    }
}
</script>
<style>
  #avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  #avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  #avatar-uploader .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 80px;
    height: 80px;
    line-height: 80px !important;
    text-align: center;
  }
  #avatar-uploader .avatar {
    width: 80px;
    height: 80px;
    display: block;
  }
</style>
<style lang='scss'>
    .publish{
        .publish_warp{
            .el-input__inner{
                height: px2rem(36) !important;
                width: 80%;
            }
            .el-select{
                width: 80%;
                .el-input__inner{
                    width: 100% !important;
                }
            }
        }
        .rich_text{
            .el-form-item__content{
                line-height: 20px !important;
            }
        }
        .el-message-box__btns{
            button:nth-child(1){
                background: #15BE9D !important;
            }
        }
        .previewBtn{
            background: #547BE8;
            color: #ffffff;
            font-size: px2rem(14);
            width: px2rem(80);
            padding: 0;
            line-height: px2rem(36);
            height: px2rem(36);
        }
        .saveDraftBtn{
            background: #F0F1F5;
            color: #707073;
            font-size: px2rem(14);
            width: px2rem(80);
            padding: 0;
            line-height: px2rem(36);
            height: px2rem(36);
        }
    }

</style>

<style lang='scss' scoped>
</style>