<template>
    <div class="leftTree">
        <el-menu
        default-active="1"
        class="el-menu-vertical-demo el_menu_warp"
        @select="handleSelect"
        background-color="#1F2D54"
        :default-openeds=openeds>
            <el-menu-item  v-for="(item,index) in msg" :key="index" :title="item.chilrenTitle" :index="item.indexUrl">
                <i class="el-icon-location"></i>
                <span slot="title">{{item.text}}</span>
            </el-menu-item>
            <!-- <el-submenu >
                <template slot="title">
                    <span >{{item.chilrenTitle}}</span>
                </template> -->
                <!-- <el-menu-item v-for="(itemClient ,index) in item.Client" :key="index" :title="itemClient.chilrenTitle" :index="itemClient.indexUrl">
                    <i class="el-icon-location"></i>
                    
                </el-menu-item> -->
                <!-- <el-menu-item title="" index="examineArticle:111">
                    <i class="el-icon-location"></i>
                    
                </el-menu-item>
                <el-menu-item title="" index="classificationArticle:111">
                    <i class="el-icon-location"></i>
                    
                </el-menu-item>
            </el-submenu>
            <el-submenu index="">
                <template slot="title">
                    <span></span>
                </template>
                <el-menu-item title="" index="publishArticle:222">
                    <i class="el-icon-location"></i>
                    
                </el-menu-item>
                <el-menu-item title="" index="examineArticle:222">
                    <i class="el-icon-location"></i>
                    
                </el-menu-item>
                <el-menu-item title="" index="classificationArticle:222">
                    <i class="el-icon-location"></i>
                    
                </el-menu-item> -->
            <!-- </el-submenu> -->
        </el-menu>
    </div>
</template>

<script>
var treeDataList = {
    articleIndex: [
        {id:7,chilrenTitle: '', indexUrl: 'publishArticle', iconUrl: '',text: ''},
        {id:8,chilrenTitle: '', indexUrl: 'examineArticle', iconUrl: '',text: ''},
        {id:9,chilrenTitle: '', indexUrl: 'classificationArticle', iconUrl: '',text: ''}
    ],
    videoIndex: [
        {id:10,chilrenTitle: '', indexUrl: 'publishVideo', iconUrl: '',text: ''},
        {id:11,chilrenTitle: '', indexUrl: 'examineVideo', iconUrl: '',text: ''},
        {id:12,chilrenTitle: '', indexUrl: 'classificationVideo', iconUrl: '',text: ''}
    ],
    noticeIndex: [
        {id:13,chilrenTitle: '', indexUrl: 'publishNotice', iconUrl: '',text: ''},
        {id:14,chilrenTitle: '', indexUrl: 'examineNotice', iconUrl: '',text: ''}
    ],
    advertIndex: [
        {id:16  ,chilrenTitle: '', indexUrl: 'advertIndex', iconUrl: '',text: ''},
    ],
    healthIndex: [
        {id:17,chilrenTitle: '', indexUrl: 'servicePack', iconUrl: '',text: ''},
        {id:18,chilrenTitle: '', indexUrl: 'serviceArg', iconUrl: '',text: ''},
    ],
    userIndex: [
        {id:19,chilrenTitle: '', indexUrl: 'userIndex', iconUrl: '',text: ''},
    ]
}

import bus from '../assets/eventBus'
export default {
    name:'leftTree',
    data() {
        return {
            msg: '',
            openeds:[],
        }
    },
    methods: {
        handleSelect(key, keyPath) {
            let jumpUrl = key.split(':')[0];
            this.$router.push({
                path: jumpUrl,
                query:{
                    jumParameter:key.split(':')[1]   //,
                }
            })
        }
    },
    beforeMount(){
        bus.$on('power',(power) => {
            // this.msg = msg
            var isTrue;
            for(var i = 0;i< power.length;i++){
                    switch(power[i].id)
                        {
                            case 1:
                                for(var j = 0;j<treeDataList.articleIndex.length;j++){
                                    isTrue = false;
                                    for(var z = 0;z<power[i].children.length;z++){
                                        if(treeDataList.articleIndex[j].id == power[i].children[z].id){
                                            isTrue = true;
                                            break;
                                        }
                                    }
                                    
                                    if(!isTrue){
                                        treeDataList.articleIndex.splice(j,1)
                                    }
                                }
                                break;
                            case 2:
                                for(var j = 0;j<treeDataList.videoIndex.length;j++){
                                    for(var z = 0;z<power[i].children.length;z++){
                                        if(treeDataList.videoIndex[j].id == power[i].children[z].id){
                                            isTrue = true;
                                            break;
                                        }
                                    }
                                    if(!isTrue){
                                        treeDataList.articleIndex.splice(j,1)
                                    }
                                }
                                break;
                            case 3:
                                for(var j = 0;j<treeDataList.noticeIndex.length;j++){
                                    for(var z = 0;z<power[i].children.length;z++){
                                        if(treeDataList.noticeIndex[j].id == power[i].children[z].id){
                                            isTrue = true;
                                            break;
                                        }
                                    }
                                    if(!isTrue){
                                        treeDataList.articleIndex.splice(j,1)
                                    }
                                }
                                break;
                            case 4:
                                for(var j = 0;j<treeDataList.advertIndex.length;j++){
                                    for(var z = 0;z<power[i].children.length;z++){
                                        if(treeDataList.advertIndex[j].id == power[i].children[z].id){
                                            isTrue = true;
                                            break;
                                        }
                                    }
                                    if(!isTrue){
                                        treeDataList.articleIndex.splice(j,1)                                        
                                    }
                                }
                                break;
                            case 5:
                                for(var j = 0;j<treeDataList.healthIndex.length;j++){
                                    for(var z = 0;z<power[i].children.length;z++){
                                        if(treeDataList.healthIndex[j].id == power[i].children[z].id){
                                            isTrue = true;
                                            break;
                                        }
                                    }
                                    if(!isTrue){
                                        treeDataList.articleIndex.splice(j,1)    
                                    }
                                }
                                break;
                            case 6:
                                for(var j = 0;j<treeDataList.userIndex.length;j++){
                                    for(var z = 0;z<power[i].children.length;z++){
                                        if(treeDataList.userIndex[j].id == power[i].children[z].id){
                                            isTrue = true;
                                            break;
                                        }
                                    }
                                    if(!isTrue){
                                        treeDataList.articleIndex.splice(j,1)    
                                    }
                                }
                                break;
                        }
                }
        })
        let routeUrl = this.$route.path.split('/')[1];  //;
        this.msg = treeDataList[routeUrl];  //
        for(let i in this.msg){
            this.openeds.push(this.msg[i].title)
        }
    }
}
</script>

<style lang='scss' scoped>
    
</style>

<style lang='scss'>
    .leftTree{
        float: left;
        width: px2rem(220);
        height: 100%;
        .el_menu_warp{
            padding: px2rem(48) 0;
            height: 100%;
            .el-submenu{
                .el-submenu__title{
                    color: #85A5FF;
                }
                .el-menu-item-group__title{
                    color: #DBE3FF;
                }
            }   
            .is-active{
                color: #85A5FF;
            }
            .el-menu-item{
                color: #DBE3FF;
                height: px2rem(46);
                line-height: px2rem(46);
            }
        }
    }
</style>
